
import { defineComponent } from 'vue'
import { defaultContent } from '@/assets/menuList'

export default defineComponent({
  name: 'viewTooltip',
  setup () {
    const widthText = defaultContent
    const base = `<ul class="list">
  <li style="justify-content:center;">
    <fx-button class="mr20" v-tooltip.topLeft="'上左 tooltip'">上左</fx-button>
    <fx-button class="mr20" v-tooltip.top="'上边 tooltip'">上边</fx-button>
    <fx-button class="mr20" v-tooltip.topRight="'上右 tooltip'">上右</fx-button>
  </li>
  <li style="justify-content:space-between;">
    <div class="left">
      <fx-button class="mb20" v-tooltip.leftTop="'左上 tooltip\\n左上 tooltip\\n左上 tooltip'">左上</fx-button>
      <fx-button class="mb20" v-tooltip.left="'左边 tooltip\\n左边 tooltip\\n左边 tooltip'">左边</fx-button>
      <fx-button class="mb20" v-tooltip.leftBottom="'左下 tooltip\\n左下 tooltip\\n左下 tooltip'">左下</fx-button>
    </div>
    <div class="right">
      <fx-button class="mb20" v-tooltip.rightTop="'右上 tooltip\\n右上 tooltip\\n右上 tooltip'">右上</fx-button>
      <fx-button class="mb20" v-tooltip.right="'右边 tooltip\\n右边 tooltip\\n右边 tooltip'">右边</fx-button>
      <fx-button class="mb20" v-tooltip.rightBottom="'右下 tooltip\\n右下 tooltip\\n右下 tooltip'">右下</fx-button>
    </div>
  </li>
  <li style="justify-content:center;">
    <fx-button class="mr20" v-tooltip.bottomLeft="'下左 tooltip'">下左</fx-button>
    <fx-button class="mr20" v-tooltip.bottom="'下边 tooltip'">下边</fx-button>
    <fx-button class="mr20" v-tooltip.bottomRight="'下右 tooltip'">下右</fx-button>
  </li>
</ul>`
    const theme = `<fx-button class="mr20" v-tooltip="'Dark'" fx-tooltip-theme="dark">Dark</fx-button>
<fx-button class="mr20" v-tooltip="'Light'" fx-tooltip-theme="light">Light</fx-button>
<fx-button class="mr20" v-tooltip="'Plain'" fx-tooltip-theme="plain">Plain</fx-button>`
    const width = `<fx-button class="mr20" v-tooltip="widthText" fx-tooltip-width="300">宽度300px</fx-button>
<fx-button class="mr20" v-tooltip.topLeft="widthText" fx-tooltip-width="600">宽度600px</fx-button>`

    return {
      base,
      theme,
      width,
      widthText
    }
  }
})
